@import "./variables";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI Emoji", "Bookman Old Style", sans-serif;
  color: $blockColorLight;
  background-color: $mainBgLight;

  &.block {
    overflow: hidden;
  }

  &.darkMode {
    color: $blockColorDark;
    background-color: $mainBgDark;
  }
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  width: $iconsSize;
  height: $iconsSize;
  cursor: pointer;
  color: $iconsColor;
}

:focus {
  outline: none;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 3vmin;
}